.header{
    height: 70px;
    display: flex;
    align-items: center;
    background-color: #131921;
    position: sticky;
    top: 0;
    z-index: 2;
}
.header_logo{
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 18px;
}
.logohead{
    text-decoration: none;
    color: #cd9042;
    font-size: 25px;
}
.header_search{
    display: flex;
    flex: 1;
    align-items: center;
}
.header_searchInput{
    height: 12px;
    padding: 10px;
    border: none;
    width: 30%;
}
.header_searchIcon{
    padding: 5px;
    height: 22px;
    border-radius: 5px;
    background-color: #cd9042;
}
.header_optionLineOne{
    font-size: 10px;
}
.header_optionLineTwo{
    font-size: 13px;
    font-weight: 800;
}
.header_optionBasket{
    display: flex;
    align-items: center;
    color: white;
}
.header_basketCount {
    margin-left: 10px;
    margin-right: 10px;
}
.header_nav{
    display: flex;
    justify-content: space-evenly;
}
.header_option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: white;
}
@media screen and (max-width: 600px){
    .header_search{
        display: none;
    }
    .header{
        justify-content:space-around;
    }
    .retur{
        display: none;
    }
}